    }
}

@media screen and (max-width:360px) {
    .careThumb {
        width: 60%;
    }
    .careThumbs {
        padding-bottom: 0;
    }
    .careJumbotron {
        padding: 2em 1em !important;
    }
    .careThumbs:after {
        display: none;
    }
    .carethumb2:after {
        position: absolute;
        content: '';
        border-left: 2px dotted #fff;
        height: 14px;
        top: 100%;
    }
}
