
.banner_logo {
    max-width: 110px;
    margin-bottom: 25px;
}
.custom_banner_heading {
    font-size: 48px;
    font-weight: 900 !important;
    line-height: 1.2;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
}
.custom_banner_left p {
    font-weight: 500;
    font-size: 16px;
}
.custom_b_btns {
    display: flex;
    align-items: center;
    margin-top: 20px;
    flex-wrap:wrap
}
.custom_b_btns a {
    color: #002970;
    font-weight: 700;
    font-size: 18px;
    padding: 19px 65px 20px 45px;
    border-radius: 35px;
    margin-right: 10px;
    margin-bottom: 10px;
    transition: all .2s;
}
.custom_b_btns a span {
    position:relative
}
.custom_b_btns a span:after {
    position:absolute;
    content:'';
    width: 13px;
    height: 13px;
    border-top: 3px solid #002970;
    border-right: 3px solid #002970;
    top: 5px;
    right: -20px;
    transform: rotate(45deg);
    transition: all .2s ;
}
.custom_b_btns a:hover span:after {
    position:absolute;
    content:'';
    right:-30px
}
.custom_b_btns a img {
    max-width:25px;
    margin-right:5px
}
.custom_b_btns a.tte_btn {
    background: #002970;
    color: #fff;
}
.custom_b_btns a:hover {
    text-decoration: none;
    color: #002970
}
.custom_b_btns a.tte_btn:hover {
	color: #fff !important;
    text-decoration: none;
    background: #00baf2;
}
.custom_b_btns a.tte_btn span:after {
    border-color:#fff
}
.home-hero {
    min-height: 100vh;
}
.care {
    background: rgb(0,47,111);
    background: -moz-linear-gradient(180deg, rgba(0,47,111,1) 13%, rgba(0,184,240,1) 87%);
    background: -webkit-linear-gradient(180deg, rgba(0,47,111,1) 13%, rgba(0,184,240,1) 87%);
    background: linear-gradient(180deg, rgba(0,47,111,1) 13%, rgba(0,184,240,1) 87%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#002f6f",endColorstr="#00b8f0",GradientType=1);
}
.careThumbs {
    position: relative;
    margin: 20px 0;
    padding-bottom:50px;
}
.careThumb {
    padding: 20px;
    border-radius: 24px;
    color: #fff;
    font-size: 18px;
    width: 38%;
    position: relative;
    margin-bottom: 10px;
    max-width: 160px;
}
.bgc_1 {
    background: #00baf2;
}
.bgc_2 {
    background: #66d6ff;
}
.careThumb .careIcon {
    max-width: 60%;
    margin-bottom: 10px;
}
.careImg {
    max-width:500px;
    margin-top: 40px;    
}
.careThumbs:after {
    position: absolute;
    content: '';
    border-bottom: 2px dotted #fff;
    width: 84%;
    top: 100%;
    left:8%;
    bottom: 0;
}
.careThumbs:before {
    position: absolute;
    content: '';
    border-left: 2px dotted #fff;
    height: 60px;
    top: 100%;
    left:50%;
    margin-left:-1px
}
.careThumb:after {
    position: absolute;
    content: '';
    border-left: 2px dotted #fff;
    height: 60px;
    top: 100%;
}
.careJumbotron {
    background: #fff !important;
    padding: 3em 4em !important;
    text-align: center;
    border-radius: 40px !important;
    position: relative;
    bottom: -100px;
    box-shadow: 0 0 10px 5px #00000024;
}
.careJumbotron h3 {
    font-size: 20px;
}
.careJumbotron h1 {
    font-size: 36px;
}
.careJumbotron h1 {
    font-weight: 700;
}

.crm_li_icon {
    min-width: 36px;
    min-height: 36px;
    background: #00baf233;
    border-radius: 50%;
    margin-right: 20px;
    position: relative;
    display: block;
}
.crm_li_icon:after {
    position: absolute;
    content: '';
    width: 20px;
    height: 20px;
    background: #00baf2;
    left: 8px;
    top: 8px;
    border-radius: 50%;
}
.crm_li_icon:before {
    position: absolute;
    content: '';
    width: 12px;
    height: 6px;
    border-bottom: 3px solid #fff;
    border-left: 3px solid #fff;
    transform: rotate(-45deg);
    top: 14px;
    left: 12px;
    z-index: 2;
}
.listing {
    margin: 0;
    padding: 0;
    list-style: none;
}
.listing li {
    padding: 4px 0 4px 16px;
    line-height: 22px;
    position: relative;
}
.listing li:before {
    position: absolute;
    content: '';
    width: 5px;
    height: 5px;
    background: #01baf2;
    left: 0;
    top: 12px;
    border-radius: 10px;
}
.crm_content {
    width: 80%;
}
.videoThumb {
    position: relative;
    border-radius: 24px;
    overflow: hidden;
}
.playBtn {
    max-width: 125px;
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    margin-left: -62px;
    margin-top: -62px;
    background: #66D6FF;
    padding: 35px 30px 35px 40px ;
    border-radius: 50%;
}
.paytmIcon {
    max-width: 50px;
    margin: 8px 0;
}
.awardLogo {
    max-width: 140px;
    margin-bottom: 20px;
}
.testimonial_item {
    border-radius: 24px;
    padding: 60px 38px;
    min-height: 340px;
}
.testimonial_item a:hover {
    text-decoration: none;
    color: #00BAF2;
}
.testimonial_logo {
    max-width: 81px;
}
.activation {
    background: url(../images/activationBg.png) no-repeat 10% 0, linear-gradient(127.11deg, #0085FE 0.45%, #00C6F0 99.14%);
    /* background-image: ;
    background-repeat: no-repeat;
    background-position: top 20%;
    background-size: contain; */
}
.activation_btn a {
    padding:15px 65px 15px 45px;
}
.activation_btn a:hover {
    text-decoration: none;
}
.activation_btn a span:after {
    border-color: #000;
}
.input_group {
    border: 1px solid #CCCCCC;
    border-radius: 6px;
    display: flex;
    align-items: center;
    background: #fff;
}
.input_group input, .input_group input:focus {
    background: none;
    border: none;
    outline:none;
    box-shadow:none
}
.input_group button {
    border: none;
    padding: 7px 12px;
    display: inline-flex;
    align-items: center;
}
.input_group button:hover, .input_group button:focus, .input_group button:active {
    background:none !important;
    border: none !important;
    outline:none !important;
    box-shadow:none !important
}
.input_group button img {
    max-width: 12px;
}
@media screen and (max-width:1200px) {
    .careThumbs:after {
        position: absolute;
        content: '';
        width: 82%;
        top: 100%;
        left: 9%;
    }
}
@media screen and (max-width:992px) {
    .custom_banner_heading {
        font-size: 30px;
    }
    .careThumb {
        margin: 15px auto;
    }
    
    .carethumb1:after {
        position: absolute;
        content: '';
        border-left: 2px dotted #fff;
        height: 245px;
        top: 100%;
    }
    .carethumb2:after {
        position: absolute;
        content: '';
        border-left: 2px dotted #fff;
        height: 64px;
        top: 100%;
    }
    .careThumbs:after {
        position: absolute;
        content: '';
        width: 50%;
        top: 100%;
        left: 25%;
    }
    .careJumbotron {
        padding: 2em 1.5em !important;
    }
    .careJumbotron h3 {
        font-size: 16px;
    }
    .careJumbotron h1 {
        font-size: 24px;
    }
}

@media screen and (max-width:767px) {
    .custom_banner {
        text-align: center;
    }
    .custom_b_btns {
        justify-content: center;
    }
    .font_md-36 {
        font-size: 36px;
    }
    .font_md-26 {
        font-size: 26px;
    }    
    .font_md-20 {
        font-size: 20px;
    }   
    .font_md-18 {
        font-size: 18px;
    }      
    .font_md-16 {
        font-size: 16px;
    }    
    .font_md-600 {
        font-weight: 600 !important;
    } 
    .font_md-500 {
        font-weight: 500;
    }
    .custom_b_btns a {
        font-size: 16px;
        padding: 15px 65px 15px 45px;
    }
    .custom_b_btns a span:after {
        width: 10px;
        height: 10px;
        top: 5px;
    }

}

@media screen and (max-width:530px) {
    .careThumb {
        width: 42%;
        padding: 10px;
        font-size: 15px;
    }
    .playBtn {
        max-width: 60px;
        margin-left: -30px;
        margin-top: -30px;
        padding: 15px 10px 15px 20px;
    }
}

@media screen and (max-width:414px) {   
    .font_sm-26 {
        font-size: 26px;
    }
    .font_sm-20 {
        font-size: 20px;
    }
    .font_sm-17 {
        font-size: 17px;
    }     
    .font_sm-15 {
        font-size: 15px;
    }    
    .font_sm-12 {
        font-size: 12px;
    }
    .font_sm-500 {
        font-weight: 500;
    }
}

@media screen and (max-width:360px) {
    .careThumb {
        width: 60%;
    }
    .careThumbs {
        padding-bottom: 0;
    }
    .careJumbotron {
        padding: 2em 1em !important;
    }
    .careThumbs:after {
        display: none;
    }
    .carethumb2:after {
        position: absolute;
        content: '';
        border-left: 2px dotted #fff;
        height: 14px;
        top: 100%;
    }
}