footer {
    position: relative;
}
.footer_links {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
}
.footer_links li {
    display: block;
}
.footer_links li a {
    display: block;
    padding: 8px 0;
    color: #000;
    font-weight: 500;
    font-size: 14px;
}
.footer_links li a:hover {
    text-decoration: none;
}
.footer_line {
    border-top: 16px solid #00BAF2;
    border-bottom: 16px solid #002970;
    position: absolute;
    bottom: 0;
    width: 100%;
}