.header {
    box-shadow: 0 2px 10px 2px #00000014;
    padding: 10px 0;
}
.main_nav li a {
    padding: 12px 18px !important;
    display: block;
    color: #000 !important;
    font-weight: 500;
    line-height: 1;
    border-radius: 4px;
}
.main_nav li a:hover {
    background: #f2f2f2;
}
.main_nav li a.signUp_btn {
    background: #00baf2;
    color: #fff !important;
    border-radius: 4px;
    font-size: 14px;
    padding-right: 40px !important;
    position: relative;
}
.main_nav li a.signUp_btn:after {
    position: absolute;
    content: '';
    width: 8px;
    height: 8px;
    border-top: 2px solid #fff;
    border-right: 2px solid #fff;
    right: 20px;
    top: 16px;
    transform: rotate(45deg);
}
.header_logo {
    max-width: 80px;
}
.custom_toggle {
    margin-right: 20px;
    border: none !important;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 35px;
    padding: 0 !important;
    height: 25px;
    margin-left: 18px;
}
.custom_toggle:focus {
    outline: none;
}
.navbar_toggler_icon {
    width: 60%;
    height: 3px;
    background: #0c2756;
    display: block;
}
.navbar_toggler_icon:before {
    position: absolute;
    content: '';
    width: 100%;
    height: 3px;
    background: #0c2756;
    top: 0;
    left: 0;
}
.navbar_toggler_icon:after {
    position: absolute;
    content: '';
    width: 80%;
    height: 3px;
    background: #0c2756;
    bottom: 0;
    left: 0;
}
.main_nav li:hover .subMenu_div {
    display: block;
}
.subMenu_div {
    position: absolute;
    list-style: none;
    background: #fff;
    padding-top: 27px;
    display: none;
    margin: 0;
    width: 280px;
    top: 57px;
    z-index: 2;
}
.subMenu {
    box-shadow: 0 5px 6px 0 #0000005c;
    list-style: none;
    padding: 0;
    margin: 0;
}
.subMenu li {
    position: relative;
}
.subMenu li a {
    display: block;
    max-width: initial;
    font-size: 14px;
}
.subMenu li a:hover {
    text-decoration: none;
}
.subMenu2_div {
    position: absolute;
    list-style: none;
    background: #fff;
    padding-top: 0;
    display: none;
    margin: 0;
    width: 290px;
    top: 0;
    left: 100%;
    z-index: 2;
}
.subMenu2 {
    box-shadow: 5px 0 6px 0 #0000005c;
    list-style: none;
    padding: 0;
    margin: 0;
}
.subMenu_div li:hover .subMenu2_div {
    display: block;
}
.main_nav li a.has_sub {
    position: relative;
    padding-right: 30px !important;
}
.has_sub:after {
    position: absolute;
    content: '';
    width: 7px;
    height: 7px;
    border-bottom: 2px solid #00baf2;
    border-right: 2px solid #00baf2;
    top: 15px;
    right: 10px;
    transform: rotate(45deg);
}

.main_nav li a.has_sub2 {
    position: relative;
    padding-right: 30px !important;
}
.has_sub2:after {
    position: absolute;
    content: '';
    width: 7px;
    height: 7px;
    border-top: 2px solid #00baf2;
    border-right: 2px solid #00baf2;
    top: 15px;
    right: 15px;
    transform: rotate(45deg);
}
@media screen and (max-width:992px) {
    .subMenu2_div, .subMenu_div {
        position: relative;
        top: 0;
        left: 0;
        width: 100%;
        padding-left: 20px;
        padding-top: 0;
    }
    .subMenu2_div .subMenu2, .subMenu_div .subMenu {
       box-shadow: none;
    }
    /* .subMenu_div li:hover .subMenu2_div {
        display: none;
    }
    .main_nav li:hover .subMenu_div {
        display: none;
    } */
}